/**  key **/
const KEY_OK = 'ok'
/**  key **/
const KEY_VALUE = 'value'
/**  resolve  key **/
const KEY_RESOLVE_ARR = 'resolveArr'
/**  reject  key **/
const KEY_REJECT_ARR = 'rejectArr'

/**
 *   Promise
 **/
let resolveCollection = {}

/**
 *   '' 
 *   Promise
 *
 *  
 *   ''  emit 
 *   resolve
 *   emit 
 *
 *  @param { String } name -  emit 
 **/
let on = function (name) {
  /**  **/
  if (typeof name !== 'string') {
    throw new TypeError('waitValue  name ')
  }
  
  return new Promise((resolve, reject) => {
    /**
     *   name  resolve 
     *  
     **/
    let target = resolveCollection[ name ] = resolveCollection[ name ] || {
      /** resolve  **/
      [ KEY_RESOLVE_ARR ]: [],
      /** reject  **/
      [ KEY_REJECT_ARR ]: []
    }
    
    /**
     *  
     **/
    if (target && target[ KEY_OK ] === true) {
      resolve(target[ KEY_VALUE ])
    }
    
    /**
     *  
     *  push resolve  reject
     *   emit 
     **/
    else {
      target[ KEY_RESOLVE_ARR ].push(resolve)
      target[ KEY_REJECT_ARR ].push(reject)
    }
  })
}

/**
 *  
 *  @param { String } name -  on 
 **/
let emit = function (name, value) {
  /**
   *   name  resolve 
   *  
   **/
  let target = resolveCollection[ name ] = resolveCollection[ name ] || {
    /** resolve  **/
    [ KEY_RESOLVE_ARR ]: [],
    /** reject  **/
    [ KEY_REJECT_ARR ]: []
  }
  
  /**  **/
  if (typeof name !== 'string') {
    throw new TypeError('waitValue  name ')
  }
  
  /**  **/
  if (target[ KEY_OK ]) {
    return console.warn(` waitValue ${ name } `)
  }
  
  /**  value  error reject **/
  if (value instanceof Error === true) {
    target[ KEY_REJECT_ARR ].forEach(reject => {
      reject(value)
    })
  }
  
  /**
   *   resolve
   *  
   *  
   **/
  else {
    target[ KEY_RESOLVE_ARR ].forEach(resolve => {
      resolve(value)
    })
    /**  **/
    target[ KEY_OK ] = true
    /** **/
    target[ KEY_VALUE ] = typeof value === 'object' ?
      JSON.parse(JSON.stringify(value))
      :
      value
    /**  resolve  reject  **/
    target[ KEY_RESOLVE_ARR ] = null
    target[ KEY_REJECT_ARR ] = null
  }
}

let waitValue = {
  on, emit
}

export { waitValue }