/**  **/
const TYPE = {
  LOG: 'log',
  INFO: 'info',
  WARN: 'warn',
  ERROR: 'err'
}

/**  **/
const COLOR = {
  [ TYPE.LOG ]: '#4CAF50',
  [ TYPE.INFO ]: '#2196F3',
  [ TYPE.WARN ]: '#F57C00',
  [ TYPE.ERROR ]: '#FF5252'
}

/**
 *  
 *  @param { String }         title  - 
 *  @param { String | Array } [desc] - 
 *  @param { String }         [type] -  log infowarnerr
 **/
const log = function (options) {
  let { type, title, desc } = options
  let color = COLOR[ type ] || COLOR[ TYPE.LOG ]
  
  /**  **/
  if (typeof options === 'string' || typeof options === 'number') {
    title = options
    desc = []
  }
  
  /**  **/
  else {
    let titleIsNumber = typeof title === 'number'
    let titleIsStr = typeof title === 'string'
    let descIsArr = desc instanceof Array
    let descIsErr = desc instanceof Error
    
    /**
     *  
     *    desc  Error  type  err
     **/
    color = descIsErr ? COLOR[ TYPE.ERROR ] : color
    
    /** title  **/
    if (!title) {
      throw new TypeError('title ')
    }
    if (titleIsNumber === false && titleIsStr === false) {
      throw new TypeError('title  String  Number ')
    }
    
    /**  desc  **/
    if (descIsArr === false) {
      if (desc) {
        desc = [ desc ]
      }
      else {
        desc = []
      }
    }
  }
  
  /**  **/
  console.log('')
  console.log('=============================================================')
  console.log(`%c${ title }`, `color: white; font-size: 12px; background-color: ${ color }; padding: 2px 5px; border-radius: 2px`)
  
  /**  desc  **/
  if (desc.length) {
    console.log('-')
    desc.forEach(function (desc) {
      console.log(desc)
    })
    console.log('-')
  }
  
  console.log('=============================================================')
  console.log('')
}

export { log }